.footer {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  background-image: url('./13.jpg'); /* Replace 'your-image-url.jpg' with the path to your background image */
  background-size: cover;
  background-position: center;
  color: #fff;
  padding: 30px 20px;
  flex-direction: column-reverse;
  position: relative;
  overflow: hidden;
}

.footer-section {
  flex: 1 1 25%;
  margin-bottom: 20px;
}

.footer-section img {
  max-width: 100%;
  height: auto;
}

.contact, .social {
  flex: 1 1 100%;
}

.contact h3, .social h3 {
  margin-bottom: 10px;
}

.contact p, .social p {
  margin: 5px 0;
}

.social-icons {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.icon-link {
  margin: 0 10px;
  font-size: 24px;
  color: #fff;
  transition: transform 0.3s ease;
}

.icon-link:hover {
  transform: translateY(-5px);
}

/* Animation */
.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.8) 100%);
  z-index: 1;
  opacity: 0;
  transition: opacity 0.5s ease;
}

.footer:hover::before {
  opacity: 1;
}

/* Media Queries */
@media screen and (max-width: 768px) {
  .footer-section {
    flex: 1 1 50%;
  }
}

@media screen and (max-width: 480px) {
  .footer-section {
    flex: 1 1 100%;
  }
}
